// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store';

//自定义组件
import IntervalInput from '@/components/common/IntervalInput.vue'
import ShopInput from '@/components/common/form/ShopInput.vue'
import ShopItem from '@/components/common/form/ShopItem.vue'
import ShopForm from '@/components/common/form/ShopForm.vue'
Vue.component("IntervalInput", IntervalInput);//全局自定义组件
Vue.component("ShopInput", ShopInput);//全局自定义组件
Vue.component("ShopItem", ShopItem);//全局自定义组件
Vue.component("ShopForm", ShopForm);//全局自定义组件

//公共样式
import './assets/styles/index.scss';

//iconfont
import '../static/iconfont/iconfont.css'

//引入vant
import Vant, { Lazyload } from 'vant';
import 'vant/lib/index.css';//vant样式

import './permission' // permission control

Vue.use(Vant);
Vue.use(Lazyload, {
  lazyComponent: true
});

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



