import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/pages/layout'

Vue.use(Router)

const routes = [
  { path: '/', redirect: '/home' },
  {
    path: '*',
    redirect: { name: "home" },
    component: layout,
    children: [
      {
        path: "home",
        name: "home",
        meta: { title: '中医文献馆文创商城' },
        component: () => import('@/pages/home/index'),
      },
      {
        path: "personal",
        name: "personal",
        meta: {
          title: '我的',
          requiresAuth: true
        },
        component: () => import('@/pages/personal/index'),
      },
      {
        path: "shoppingCart",
        name: "shoppingCart",
        meta: { title: '购物车' },
        component: () => import('@/pages/shoppingCart/index'),
      }
    ]
  },
  //搜索
  {
    path: "/search",
    name: "search",
    meta: { title: '搜索' },
    component: () => import('@/pages/search/index'),
  },
  //订单
  {
    path: "/orders/list/:id",
    name: "ordersList",
    meta: {
      title: '订单',
      requiresAuth: true
    },
    component: () => import('@/pages/orders/index'),
  },
  {
    path: "/orders/confirm",
    name: "confirmOrders",
    meta: {
      title: '确认订单',
      requiresAuth: true
    },
    component: () => import('@/pages/orders/confirm'),
  },
  {
    path: "/orders/info",
    name: "confirmInfo",
    meta: {
      title: '订单详情',
      requiresAuth: true
    },
    component: () => import('@/pages/orders/info'),
  },
  //商品
  {
    path: "/goods",
    name: "goods",
    meta: { title: '商品列表' },
    component: () => import('@/pages/goods/index'),
  },
  {
    path: "/goods/info",
    name: "goodsInfo",
    meta: { title: '商品详情' },
    component: () => import('@/pages/goods/info'),
  },
  //支付结果
  {
    path: "/pay",
    name: "pay",
    meta: {
      title: '支付结果',
      requiresAuth: true
    },
    component: () => import('@/pages/pay/index'),
  },
  //物流
  {
    path: "/logistics/info",
    name: "logisticsInfo",
    meta: {
      title: '查看物流',
      requiresAuth: true
    },
    component: () => import('@/pages/logistics/info'),
  },
  //交易物流
  {
    path: "/logistics",
    name: "logistics",
    meta: {
      title: '交易物流',
      requiresAuth: true
    },
    component: () => import('@/pages/logistics/index'),
  },
  //推广大使
  {
    path: "/ambassador",
    name: "ambassador",
    meta: {
      title: '推广大使',
      requiresAuth: true
    },
    component: () => import('@/pages/ambassador/index'),
  },
  //优惠详情
  {
    path: "/ambassador/discounts/info",
    name: "ambassadorDiscountsInfo",
    meta: {
      title: '优惠码详情',
      requiresAuth: true
    },
    component: () => import('@/pages/ambassador/discounts/info'),
  },
  //佣金提取明细
  {
    path: "/ambassador/brokerage/info",
    name: "ambassadorBrokerageInfo",
    meta: {
      title: '佣金提取明细',
      requiresAuth: true
    },
    component: () => import('@/pages/ambassador/brokerage/info'),
  },
  //佣金提取
  {
    path: "/ambassador/brokerage/extract",
    name: "ambassadorBrokerageExtract",
    meta: {
      title: '佣金提取',
      requiresAuth: true
    },
    component: () => import('@/pages/ambassador/brokerage/extract'),
  },
  //选择银行卡
  {
    path: "/bankcard",
    name: "bankcard",
    meta: {
      title: '选择银行卡',
      requiresAuth: true
    },
    component: () => import('@/pages/bankcard/index'),
  },
  //添加银行卡
  {
    path: "/bankcard/create",
    name: "bankcardCreate",
    meta: {
      title: '添加银行卡',
      requiresAuth: true
    },
    component: () => import('@/pages/bankcard/edit'),
  },
  //收货地址
  {
    path: "/address",
    name: "address",
    meta: {
      title: '收货地址',
      requiresAuth: true
    },
    component: () => import('@/pages/address/index'),
  },
  //完善信息
  {
    path: "/address/create",
    name: "addressCreate",
    meta: {
      title: '完善信息',
      requiresAuth: true
    },
    component: () => import('@/pages/address/edit'),
  },
]

export default new Router({
  mode: 'history',
  routes
})



