import { getToken, setToken, removeToken } from '@/utils/auth'

const user = {
    state: {
        token: getToken(),
    },

    mutations: {
        SET_TOKEN: (state, token) => {
            state.token = token
        },
    },

    actions: {
        Login_token({ commit }, userInfo) {
            const { token } = userInfo
            return new Promise((resolve, reject) => {
                if (token) {
                    setToken(token)
                    commit('SET_TOKEN', token)
                    resolve()
                } else {
                    reject('token:undefind')
                }
            })
        },
        // 前端 登出
        FedLogOut({ commit }) {
            return new Promise(resolve => {
                commit('SET_TOKEN', '')
                removeToken()
                resolve()
            })
        },
        // 后端 登出
        LogOut({ commit, state }) {
            return new Promise((resolve, reject) => {
                // logout({ token: state.token, agent_id: App.ID }).then(() => {
                //     commit('SET_TOKEN', '')
                //     commit('SET_ROLES', [])
                //     removeToken()
                //     resolve()
                // }).catch(error => {
                //     reject(error)
                // })
            })
        },
    }
}

export default user



