<template>
  <div class="shop-input">
    <input :type="type" :style="{width}" :value="value" :placeholder="placeholder" @input="updateVal($event.target.value)" />
  </div>
</template>

<script>
export default {
  components: {},
  props: {
    width:{
      type:String,
      default:"auto"
    },
    value: {
      type: String,
      default: "",
    },
    type:{
      type: String,
      default: "text",
    },
    placeholder:{
      type: String,
      default: "",
    }
  },
  data() {
    return {};
  },
  watch: {},
  computed: {},
  methods: {
    updateVal: function (val) {
      // 2、手动触发父组件的input事件并将值传给父组件
      this.$emit("input", val);
    },
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
.shop-input {
  input {
    display: block;
    box-sizing: border-box;
    width: 100%;
    min-width: 0;
    margin: 0;
    padding: 0;
    color: #323233;
    line-height: inherit;
    text-align: left;
    background-color: transparent;
    border: 0;
    resize: none;
  }
}
</style>


