/**
 * @param {Sting} url
 * @param {Sting} redirectUrl
 * @param {Sting} agentID
 */

export default function jump(url, redirectUrl, agentID) {
    window.location.href =
        `${url}?redirect_url=${redirectUrl}&agentID=${agentID}`;
}


