<template>
  <div class="app-container">
    <app-main />
    <Navbar />
  </div>
</template>

<script>
import { AppMain, Navbar } from "./components";
export default {
  components: { AppMain, Navbar },
  props: {},
  data() {
    return {};
  },
  watch: {},
  computed: {},
  methods: {},
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
.app-container {
  width: 100%;
}
</style>


