
const app = {
    state: {
        nav_height: 0,
    },
    mutations: {
        SET_NAV_HEIGHT: (state, height) => {
            state.nav_height = height
        },
    },
    actions: {
        setHeight({ commit }, Info) {
            commit('SET_NAV_HEIGHT', Info.height)
        }
    }
}

export default app



