<template>
  <div :class="state?'shop-input-err':'shop-input'">
    <input
      ref="input"
      v-if="readonly"
      :type="type"
      :value="currentValue"
      :placeholder="placeholder"
      readonly="readonly"
      @input="handleInput"
      @blur="handleBlur"
      @change="handleChange(currentValue)"
    />
    <input
      ref="input"
      v-else
      :type="type"
      :value="currentValue"
      :placeholder="placeholder"
      @input="handleInput"
      @blur="handleBlur"
      @change="handleChange(currentValue)"
    />
    <div class="rightIcon" v-show="rightIcon">
      <i :class="'iconfont '+rightIcon" />
    </div>
  </div>
</template>

<script>
import Emitter from "@/mixins/emitter.js";
export default {
  mixins: [Emitter],
  props: {
    max: {
      type: String,
      default: "",
    },
    state: {
      type: Boolean,
      default: false,
    },
    rightIcon: {
      type: String,
      default: "",
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    value: {
      type: String,
      default: "",
    },
    type: {
      type: String,
      default: "text",
    },
    placeholder: {
      type: String,
      default: "",
    },
  },
  data() {
    return { currentValue: this.value, id: this.label };
  },
  watch: {
    value(value) {
      if (this.max) {
        if (value <= Number(this.max)) {
          this.currentValue = value;
        } else {
          this.currentValue = this.max;
        }
      } else {
        this.currentValue = value;
      }
    },
  },
  computed: {},
  methods: {
    handleChange(value) {
      if (this.max) {
        if (value <= Number(this.max)) {
          this.currentValue = value;
        } else {
          this.currentValue = this.max;
        }
      } else {
        this.currentValue = value;
      }
    },
    handleInput: function (e) {
      // 2、手动触发父组件的input事件并将值传给父组件
      const value = e.target.value;
      this.currentValue = value;
      this.$emit("input", value);
      this.dispatch("ShopItem", "form-change", value);
    },
    handleBlur() {
      this.dispatch("ShopItem", "form-blur", this.currentValue);
    },
  },
  created() {},
  mounted() {
    if (this.$parent.labelFor) this.$refs.input.id = this.$parent.labelFor;
  },
};
</script>
<style lang="scss" scoped>
.shop-input {
  display: flex;
  width: 100%;
  line-height: 22px;
  border-radius: 5px;
  border: solid 1px #cccccc;
  padding: 8px 16px;
  color: #999999;
  font-size: 14px;
  input {
    display: block;
    box-sizing: border-box;
    width: 100%;
    min-width: 0;
    margin: 0;
    padding: 0;
    color: #323233;
    line-height: inherit;
    text-align: left;
    background-color: transparent;
    border: 0;
    resize: none;
  }
  .rightIcon {
    i {
      font-size: 15px;
      color: #999999;
    }
  }
}
.shop-input-err {
  border: solid 1px #ee0a24;
  color: #ee0a24;
  i {
    color: #ee0a24 !important;
  }
  input::-webkit-input-placeholder {
    color: #ee0a24;
  }
  input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #ee0a24;
  }
  input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #ee0a24;
  }
  input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #ee0a24;
  }
  @extend .shop-input;
}
</style>


