import store from '../store'
import App from "./configApp";
import jump from "./loginWebsite";

function isAuthorization(to, next) {
    const uri = App.uri
    const ID = App.ID
    let href = window.location.href
    if (to.query.token) {
        //登录成功携带token
        store.dispatch("Login_token", { token: decodeURIComponent(to.query.token) }).then(() => {
            next({ path: to.path, query: {}, replace: true })
        });
    } else {
        // if (href.indexOf('?') !== -1) {
        //     console.log(href)
        //     var last = href.indexOf('?');
        //     href = href.substring(0, last)
        // }
        jump(uri, encodeURIComponent(href.match(/(\w+):\/\/([^/:]+)(:\d*)?/)[0] + to.path), ID)
    }
}

export default isAuthorization


