<template>
  <van-tabbar id="tabbar" active-color="#7e3330" inactive-color="#000" route>
    <van-tabbar-item
      replace
      to="/home"
      icon="iconshangcheng"
      icon-prefix="iconfont iconshangcheng"
    >商城</van-tabbar-item>
    <van-tabbar-item
      @click="initHeight"
      replace
      to="/shoppingCart"
      icon="icongouwuche"
      icon-prefix="iconfont icongouwuche"
    >购物车</van-tabbar-item>
    <van-tabbar-item replace to="/personal" icon="iconwode" icon-prefix="iconfont iconwode">我的</van-tabbar-item>
  </van-tabbar>
</template>

<script>
import elementResizeDetectorMaker from "element-resize-detector";
export default {
  name: "Navbar",
  data() {
    return {
      active: 0,
      icon: {
        active: "https://img.yzcdn.cn/vant/user-active.png",
        inactive: "https://img.yzcdn.cn/vant/user-inactive.png",
      },
    };
  },
  methods: {
    isWeiXin() {
      var ua = window.navigator.userAgent.toLowerCase();
      if (ua.match(/MicroMessenger/i) == "micromessenger") {
        return true;
      } else {
        return false;
      }
    },
    initHeight() {
    let tabbar = document.getElementById("tabbar");
      this.$store.dispatch("setHeight", {
        height: tabbar.offsetHeight,
      });
    },
  },
  mounted() {
    const erd = elementResizeDetectorMaker();
    const tabbar = document.getElementById("tabbar");
    if (this.isWeiXin()) {
      this.initHeight();
    }
    const _this = this;
    erd.listenTo(tabbar, (element) => {
      _this.$nextTick(() => {
        //监听到事件后执行的业务逻辑
        // alert(element.offsetHeight)
        _this.$store.dispatch("setHeight", { height: element.offsetHeight });
      });
    });
  },
};
</script>

<style lang="scss" scoped>
.van-tabbar {
  /deep/ .van-tabbar-item__icon {
    margin-bottom: 0;
    i {
      font-size: 25px;
    }
  }
}
</style>


